@import '../utils/variables';

.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #e5e5e5;
  font-weight: 700;
  text-transform: uppercase;
  color: black;
  overflow: auto;

  &__top {
    margin-bottom: 32px;
    position: sticky; 
    top: 0;
    z-index: 1;
    background-color: #e5e5e5;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__bottom {
    margin-top: 24px;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    color: inherit;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
    margin-bottom: 16px;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    color: inherit;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;
  }
}
